<template>
  <div id="Train">
    <div class="querys">
    	<div class="dept">
    		<p class="tag">出发城市</p>
    		<TrainCityInput v-model="query.fromStation"/>
    	</div>
    	<div @click="ex">
    		<span class='iconfont'>&#xe61c;</span>
    	</div>
    	<div class="arr">
    		<p class="tag">达到城市</p>
    		<TrainCityInput v-model="query.arrStation"/> 
    	</div>
    </div>
    <div class="dateWarp">
    	<el-date-picker	      
	      type="date"
	      class="date"
	      v-model="query.departTime"
	      size="small"
	      placeholder="选择日期">
	    </el-date-picker>
    </div>
    <div class="btnWarp">
    	<el-button @click="search" class="search" type="primary">搜索</el-button>
    </div>
  </div>
</template>

<script>
import { formDate } from '@/utils/util'
import TrainCityInput from '@/components/Train-city'
export default {
  data () {
    return {
      query:{
      	fromStation:'北京',
      	arrStation:'上海',
      	departTime:new Date()
      }
    }
  },
  methods:{
  	search(){
  		this.query.departTime = formDate(this.query.departTime);
  		this.$router.push({path:'/train',query:this.query})
  	},
  	ex(){
  		let ex = this.query.deptCity;
  		this.query.deptCity = this.query.arrCity;
  		this.query.arrCity = ex;
  	}
  },
  components:{
  	TrainCityInput
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import "../../../style/mixin.scss";
#Train{
	display:flex;
	flex-direction:column;
	height:100%;
	.querys{
		@include fj();
		.dept,.arr{
			flex:1;
		}
		.tag{
			font-size: 12px;
			color:#95a7b3;
			margin-bottom: 8px;
		}
		.iconfont{
			font-size: 22px;
			color:red;
			cursor: pointer;
			display: inline-block;
			transform: translateY(25px);
			margin:0 12px;
			user-select: none;
		}
	}
	.dateWarp{
		margin-top:10px;
		.date{
			width:100%;
		}
	}
	.btnWarp{
		flex:1;
		display:flex;
		justify-content:flex-end;
		align-items:flex-end;
		.search{
			width:150px;
			height:42px;
			background: $blue;
		}
	}
}
</style>



