<template>
	<div id="home" :style="bannerStyle">
		<TmcFooter></TmcFooter>
		<div class="content">
			<div class="main">
				<div class="menu">
					<li :class="{'active':active==item.key}" @click="choose(item.key,item.bgUrl)" v-for="item in menu">{{item.name}}</li>
				</div>
				<div class="panel">
					<Flight v-if="active==1"></Flight>
					<Hotel v-if="active==2"></Hotel>
					<Train v-if="active==3"></Train>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import main from './main';
	export default {
		...main
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import "../../style/mixin.scss";
	@import "./style.scss";	
</style>


